import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, Injector, HostListener, ViewChild, TemplateRef } from '@angular/core';


import { UserService } from 'src/app/api/controllers/User';
import { SharedService } from '../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.scss']
})
export class MyProfileComponent implements OnInit {

  user:any;
  userRole:any;

  isUserDataLoaded:boolean = false;
  profilePicUrl:string = "";

  apartmentBlockUnitId:any;

  @Input() type?:any;
  @Input() isProfile?:boolean;
  @Input() userId?:any;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private userService:UserService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  
  isNotAdmin(){
    return this.type != 'admin' ? true : false 
  }

  closeUserInfo(){
    this.isProfile = false;
    this.outputParams.emit(this.isProfile);
  }

  ngOnInit() {

  	this.user = {};

    if(this.type != 'admin'){
      this.userId = parseInt(this.cookieService.get('userId'))
    }
 let   queryParamBase = {
      userid:this.userId,
    };
  	this.userService.getUserById(queryParamBase).subscribe((res : any) => {

  		this.user = res[0];
    let  queryParamBase2 = {
        userId: this.userId,
      };
    		this.userService.getRolesByUserId(queryParamBase2).subscribe((role : any) => {

    			this.userRole = role[0];
    			this.isUserDataLoaded = true;

    		},
  		error => {
  			console.log(error);
  		});


  	},
  	error => {
  		console.log(error);
  	});



  this.sharedService.userdetailscast.subscribe(user => this.user = user);

  this.sharedService.profilepiccast.subscribe( profilePicUrl => {
    this.profilePicUrl = profilePicUrl
  })


  }

  ngOnChanges(changes: SimpleChanges) {

   if(this.type != 'admin'){
      this.userId = parseInt(this.cookieService.get('userId'))
   }

   console.log(this.userId);

   this.userService.getUserById(this.userId).subscribe((res : any) => {

      this.user = res[0];

      console.log(this.user);

        this.userService.getRolesByUserId(this.userId).subscribe((role : any) => {

          this.userRole = role[0];
          this.isUserDataLoaded = true;

        },
      error => {
        console.log(error);
      });


    },
    error => {
      console.log(error);
    });

  }

}
